













import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
import { IDB } from '@/dto/DataBaseList';
import DB from './DB.vue';
import router from '@/router';

@Component({
    components: { DB }
})
export default class DBList extends Vue {
    @Prop() list!: IDB[];
    @Prop() active!: string;

    select(db: string) {
        router.push({ path: `/${db}` })
    }
}
